/* Layout */
.layout {
  margin: 0 auto;
  max-width: 1200px;
  padding: 20px;
}

/* Filters */
.filters {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.filters h6 {
  margin-bottom: 5px;
}

.filters .switch-icons {
  display: flex;
}

.filters .switch-icons .active-icon {
  color: #ff5722; /* Orange color */
}

.filters .switch-icons .inactive-icon {
  color: #9e9e9e; /* Grey color */
}

/* Content */
.content {
  margin-bottom: 20px;
}

/* Modal */
.modal {
  max-width: 500px;
  margin: 0 auto;
}

/* Spinner */
.spinner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Button */
.btn {
  padding: 8px 16px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  background-color: #03a9f4; /* Blue color */
  color: white;
}

.btn-primary {
  background-color: #03a9f4; /* Blue color */
}

/* Table */
.ant-table {
  width: 100%;
}

/* Form */
.form {
  max-width: 400px;
  margin: 0 auto;
}

.form label {
  margin-bottom: 5px;
}

.form input,
.form select {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.form .d-flex {
  display: flex;
  justify-content: flex-end;
}

/* Icons */
.mx-2 {
  margin: 0 2px;
}
